// ====================================================================
// Grid
// ====================================================================

.container {
  margin: 0 auto;
  width: 90%;
  max-width: 1500px;

  @include desktop {
    width: 80%; }


  @include extra-large; }

.row {
  display: flex;
  flex-wrap: wrap;

  .col {
    min-height: 1px; }

  &::after {
    content: "";
    display: table;
    clear: both; } }

@include extra-small {
  @include grid-colms(xs); }


@include mobile {
  @include grid-colms(s); }


@include tablet {
  @include grid-colms(m); }


@include desktop {
  @include grid-colms(l); }


@include extra-large {
  @include grid-colms(xl); }
