// ====================================================================
// Quote
// ====================================================================

.quote {
  box-sizing: border-box;
  max-width: 1200px;
  margin: auto;
  text-align: center;
  color: #fff;
  border-radius: $base-border-radius;
  overflow: hidden;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.3);

  @include extra-small {
    width: 95%;
    margin-bottom: 3em; }


  @include mobile {
    width: 65%;
    margin-bottom: 3em; }


  @include tablet {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 65%;
    margin: auto; }


  @include desktop {
    width: 55%;
    top: 50%; }


  @include extra-large {
    width: 50%; }


  //display: grid
 }  //grid-template-columns: 60% 1fr

.quote_img {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.quote_cite {
  padding: 1em;
  background-color: #FF0000; }

