// ====================================================================
// Breakpoints
// ====================================================================

@mixin extra-small {
  @include breakpoint($mobile) {
    @content; } }

@mixin mobile {
  @include breakpoint-above($mobile) {
    @content; } }

@mixin tablet {
  @include breakpoint-above($tablet) {
    @content; } }

@mixin desktop {
  @include breakpoint-above($desktop) {
    @content; } }

@mixin extra-large {
  @include breakpoint-above($extra-large) {
    @content; } }

@mixin breakpoint-above($size) {
  @media screen and (min-width: $size) {
    @content; } }

@mixin breakpoint($size) {
  @media screen and (max-width: $size) {
    @content; } }

// Grid colums mixin
@mixin grid-colms($class) {
  @for $i from 1 through 12 {
    .col.#{$class + $i} {
      width: $width / (12 / $i); } } }
